import React, { useState } from "react";
import { PetContainer } from "../Pet/PetContainer";

import "./App.css";

export function App() {
  const [ output, setOutput ] = useState("");

  function touchStart(e) {
    const { targetTouches: touches } = e;
    console.log(e);

    const positions = [...touches].map(({ clientX, clientY }) => ({ clientX, clientY }));
    setOutput(JSON.stringify(positions, null, 2));
  }

  function touchEnd(e) {
    setOutput("");
  }

  return (
    <>
      <div
        id="test"
        onTouchStart={touchStart}
        onTouchMove={touchStart}
        onTouchEnd={touchEnd}
        onTouchCancel={touchEnd}
      ></div>
      <pre>{output}</pre>

      <div className="App">
        <PetContainer petId="cynthia" />
        <PetContainer petId="bassi" />
      </div>
    </>
  );
}
